<template>
  <div class="lineage">
    <div class="toobar">
      <el-select-v2 v-model="filterString" :options="options" filterable clearable size="small" placeholder="请选择表名称" style="width: 300px; margin-right: 6px;"></el-select-v2>
      <el-button type="primary" size="small" @click="handleFullscreen">FullScreen</el-button>
      <input v-model="zoom" type="range" min="1" max="100" step="1" />
      <span>{{zoom}}</span>
    </div>
    <div id="canvas" class="canvas"></div>
  </div>
</template>

<script lang="ts" setup>
import {ref, onBeforeUnmount, onMounted, watch} from 'vue'
import {ElSelectV2, ElButton} from 'element-plus'
import {Lineage as Graph} from '@ej/erg-renderer/src'
import mockData from './mock'

const zoom = ref('50')
const filterString = ref('')

let graph
const mountGraph = () => {
  const el = document.getElementById('canvas')
  graph = new Graph({
    el,
    data: mockData,
    graphStartX: 40,
    graphStartY: 40,
    useThumbnail: true,
  })
  graph.on('line:click', (id) => console.log('line:click', id))
  graph.on('node:click', (id) => console.log('node:click', id))
  graph.on('node-attr:click', (id) => console.log('node-attr:click', id))
  graph.on('graph:move', (ev) => console.log('graph:move', ev))
}
const unmountGraph = () => {
  graph.destroy()
}
const updateGraph = () => {
  graph.update()
}
const handleFullscreen = () => {
  graph.fullscreen()
}

const options = ref([])
const setOptions = () => {
  const result = []
  for (let i = 0; i < mockData.table.length; i++) {
    const node = mockData.table[i]
    result.push({
      value: node.id,
      label: node.enName,
    })
    for (let j = 0; j < node.attributes.length; j++) {
      const attr = node.attributes[j]
      result.push({
        value: attr.id,
        label: attr.enName,
      })
    }
  }
  options.value = result
}

watch(() => filterString.value, (value) => graph.viewTo(value))
watch(() => zoom.value, (value) => graph.zoom(value))
onMounted(() => {
  mountGraph()
  setOptions()
})
onBeforeUnmount(unmountGraph)
</script>

