  height: 100%;
  display: flex;
  flex-direction: column;
}

.toobar {
  background: #ccc;
  padding: 5px;
  display: flex;
  align-items: center;
  justify-between: center;
}

.canvas {
  flex-grow: 1;
  width: 100%;
  height: 100%;
  /* border: 1px solid #000; */
  margin: auto;
  box-sizing: border-box;
}
</style>