import {Editable} from '../types/enum'
import type {LineageOptions, LineageData, Table, Relationship, CanvasEvent, Id} from '../types/index'

import {EventCenter} from '../core/shared/event'
import {Canvas, BackgroundType} from '../core/shared/canvas'
import {NodeLineage as Node} from '../core/materials/node'
import {LineBezier as Line} from '../core/materials/line'
import {setBrowserCompatibility} from '../core/utils/browser'
import {isNumber, isBoolean} from '../core/utils/verify'
import {nextTick} from '../core/utils/dom'

import {Plugins, PluginsEnum} from '../plugins'

import {preloadLineageImages} from '../core/shared/image'
  
export class Lineage extends EventCenter {
  private el: HTMLElement
  private canvas: Canvas

  private data: LineageData
  private nodeMap: Map<Id, Node>
  private attrMap: Map<Id, Node>
  private lineMap: Map<Id, Line>

  private lineageMap: Map<Id, Id[]>
  private nodeMatrix: Id[][]

  private editable: Editable
  private graphStartX = 20
  private graphStartY = 20
  private hoverTargetId: Id
  private activeTargets: Id[]

  private _bindEventResize: Function
  private _bindEventWheel: Function
  private _bindEventMouseup: Function
  private _bindEventMousedown: Function
  private _bindEventMousemove: Function

  private plugins: Plugins
  private useThumbnail: boolean

  constructor (input: LineageOptions) {
    super()
    setBrowserCompatibility()
    this.analysisOptions(input)
    this.initCanvasDom()
    this.mountCanvasEvents()
    this.mountPlugins()
    preloadLineageImages().then(() => this.update())
  }

  private analysisOptions (input: LineageOptions) {
    const {
      el,
      data = {
        table: [],
        relationship: [],
      },
      editable = Editable.Edit,
      graphStartX,
      graphStartY,
      useThumbnail,
    } = input

    if (el) this.el = el
    if (editable) this.editable = editable
    if (isNumber(graphStartX)) this.graphStartX = graphStartX
    if (isNumber(graphStartY)) this.graphStartY = graphStartY
    if (data) this.updateData(data)
    if (isBoolean(useThumbnail)) this.useThumbnail = useThumbnail
  }

  private updateData (data: LineageData) {
    if (!data) return
    this.data = data
    this.nodeMap = new Map()
    this.attrMap = new Map()
    this.lineMap = new Map()
    this.lineageMap = new Map()
    this.nodeMatrix = []
  }

  private initCanvasDom () {
    this.canvas = new Canvas({
      mountEl: this.el,
      backgroundType: BackgroundType.Color,
    })
  }

  private mountCanvasEvents () {
    this._bindEventMousedown = this.onMouseDown.bind(this)
    this._bindEventMousemove = this.onMouseMove.bind(this)
    this._bindEventMouseup = this.onMouseUp.bind(this)
    this._bindEventResize = this.onResize.bind(this)
    this._bindEventWheel = this.onWheel.bind(this)
  
    this.canvas.on('canvas:resize', this._bindEventResize)
    this.canvas.on('canvas:wheel', this._bindEventWheel)
    this.canvas.on('canvas:mouseup', this._bindEventMouseup)
    this.canvas.on('canvas:mousedown', this._bindEventMousedown)
    this.canvas.on('canvas:mousemove', this._bindEventMousemove)
  }

  private unmountCanvasEvents () {
    this.canvas.off('canvas:resize', this._bindEventResize)
    this.canvas.off('canvas:wheel', this._bindEventWheel)
    this.canvas.off('canvas:mouseup', this._bindEventMouseup)
    this.canvas.off('canvas:mousedown', this._bindEventMousedown)
    this.canvas.off('canvas:mousemove', this._bindEventMousemove)
  }

  private freeVariable () {
    this.el = null
    this.editable = null
    this.data = null
    this.nodeMap = null
    this.attrMap = null
    this.lineMap = null
    this.canvas = null
    this.hoverTargetId = null
    this.activeTargets = []
  }

  private mountPlugins() {
    this.plugins = new Plugins()
    
    if (this.useThumbnail) {
      // 初始化全景缩略图
      this.plugins.use(PluginsEnum.Thumbnail, {
        mountEl: this.canvas.boxEl,
        mainCanvas: this.canvas,
      })
    }
  }

  private unmountPlugins() {
    this.plugins.destroy()
  }

  public update(data?: LineageData) {
    // if (this.renderPending) return
    // this.renderPending = true
    // requestAnimationFrame(() => {
    if (!this.canvas) return
    this.updateData(data)
    this.canvas.clearCacheCanvas()
    this.initNodes()
    this.initLines()
    this.makeNodeMatrix()
    this.renderNodes()
    this.renderLines()
    this.canvas.update()
    this.updateThumbnail()
      // this.renderPending = false
    // })
  }

  private async updateThumbnail () {
    const thumbnail = this.plugins.get(PluginsEnum.Thumbnail)
    if (!thumbnail) return false

    await nextTick()
    const { width: viewW, height: viewH } = this.canvas.getRenderSize()
    let minX = 0, minY = 0, maxX = viewW, maxY = viewH
    this.nodeMap.forEach(node => {
      const { x, y, width, height } = node
      if (x < minX) minX = x
      if (y < minY) minY = y
      if (x + width > maxX) maxX = x + width
      if (y + height > maxY) maxY = y + height
    })

    thumbnail.draw(minX, minY, maxX, maxY, (ctx) => {
      this.nodeMap.forEach(node => {
        const { x, y, width, height, bgColor, borderColor } = node
        ctx.save()

        ctx.fillStyle = bgColor || '#fff'
        ctx.fillRect(x, y, width, height)
        ctx.strokeStyle = borderColor || '#000'
        ctx.strokeRect(x, y, width, height)
        ctx.restore()
      })
    })
  }

  private renderNodes () {
    const {
      nodeMatrix,
      nodeMap,
      graphStartX,
      graphStartY,
    } = this
    for (let i = 0; i < nodeMatrix.length; i++) {
      const columns = nodeMatrix[i]
      let y = graphStartY
      for (let j = 0; j < columns.length; j++) {
        const nodeId = columns[j]
        const node = nodeMap.get(nodeId)
        node.updateOptions({
          x: (node.width + 100) * i + graphStartX,
          y,
        })
        const isVisible = this.canvas.isVisible(node.boundingBox)
        if (isVisible) node.draw()

        y += (node.expandLeft ? node.attributes.length * node.attrHeight : 0) + node.titleHeight + 20
      }
    }
  }

  /**
   * 实例化节点信息
   * @description 该方法调用后：nodeMap/attrMap可访问
   * @returns 
   */
  private initNodes () {
    const table = this.data?.table
    if (!table?.length) return
    for (let i = 0; i < table.length; i++) {
      const it = table[i]
      let node = this.nodeMap.get(it.id)
      if (!node) {
        node = new Node(this.canvas.cacheCtx, it)
        this.nodeMap.set(it.id, node)

        for (let j = 0; j < node.attributes.length; j++) {
          const attr = node.attributes[j]
          this.attrMap.set(attr.id, node)
        }
      }
      this.lineageMap.set(node.id, [])
      // const isVisible = this.canvas.isVisible(node.boundingBox)
      // if (isVisible) node.draw()
    }
  }

  private initLines () {
    const {
      nodeMap,
      attrMap,
      lineageMap,
      data: { relationship },
    } = this
    if (!relationship?.length) return
    for (let i = 0; i < relationship.length; i++) {
      const it = relationship[i]
      let line = this.lineMap.get(it.id)
      const sourceNode = nodeMap.get(it.from) || attrMap.get(it.from)
      const targetNode = nodeMap.get(it.to) || attrMap.get(it.to)
      if (!sourceNode || !targetNode) continue

      if (!line) {
        line = new Line(this.canvas.cacheCtx, it)
        this.lineMap.set(it.id, line)
      }

      const targetNodeAncestors = lineageMap.get(targetNode.id)
      const isIncludeAncestors = targetNodeAncestors.includes(sourceNode.id)
      const isSelf = sourceNode.id === targetNode.id
      if (!isIncludeAncestors && !isSelf) targetNodeAncestors.push(sourceNode.id)
      // const isVisible = this.canvas.isVisible(line.boundingBox)
      // if (isVisible) line.draw(lineOptions)
    }
  }

  private makeNodeMatrix () {
    const {nodeMatrix, lineageMap} = this
    
    let done = false
    while (!done) {
      // 每循环一次生成一个新的column
      done = true
      const upLevelNodeIds = nodeMatrix[nodeMatrix.length - 1]
      const currentColumn = []
      for (const [nodeId, nodeAncestors] of lineageMap) {
        if (nodeAncestors.length) {
          // console.log(nodeId, nodeAncestors);
          
          done = false
          if (!upLevelNodeIds) continue
          // 非第一级时：与上一级id数据逐一对比
          for (let i = 0; i < upLevelNodeIds.length; i++) {
            const id = upLevelNodeIds[i]
            const index = nodeAncestors.indexOf(id)
            if (index === -1) {

            } else {
              nodeAncestors.splice(index, 1)
              if (!nodeAncestors.length) currentColumn.push(nodeId)
            }
          }
          
        } else {
          // 第一级时：
          if (!upLevelNodeIds) currentColumn.push(nodeId)
        }
      }

      if (currentColumn.length) {
        nodeMatrix.push(currentColumn)
      } else {
        return done = false
      }
    }
    
    
  }

  private renderLines () {
    const {
      nodeMap,
      attrMap,
      data: {relationship},
    } = this
    if (!relationship?.length) return
    for (let i = 0; i < relationship.length; i++) {
      const it = relationship[i]
      const line = this.lineMap.get(it.id)
      const sourceNode = nodeMap.get(it.from) || attrMap.get(it.from)
      const targetNode = nodeMap.get(it.to) || attrMap.get(it.to)
      if (!sourceNode || !targetNode) continue

      const sourcePosition = sourceNode.getLinePosition(it.from)
      const targetPosition = targetNode.getLinePosition(it.to)

      line.updateOptions({
        startX: sourcePosition.x + sourceNode.width,
        startY: sourcePosition.y,
        endX: targetPosition.x,
        endY: targetPosition.y,
      })
      const isVisible = this.canvas.isVisible(line.boundingBox)
      if (isVisible) line.draw()
    }
  }

  // eventCanvas临时图形
  private renderTemporaryEventCanvas () {
    const {
      activeTargets,
      hoverTargetId,
      lineMap,
      nodeMap,
      attrMap,
      canvas,
      data,
    } = this
    this.renderPermanentEventCanvas()

    if (lineMap.get(hoverTargetId)) {
      const {
        startX,
        startY,
        endX,
        endY,
        type,
      } = lineMap.get(hoverTargetId)
      new Line(canvas.eventCtx, {
        startX,
        startY,
        endX,
        endY,
        type,
        lineWidth: 2,
      }).draw()
    } else if (attrMap.get(hoverTargetId)) {
      if (activeTargets?.includes(hoverTargetId)) return

      const targetNode = attrMap.get(hoverTargetId)
      const attrIndex = targetNode.attributes.findIndex(it => it.id === hoverTargetId)
      // 绘制属性高亮
      const {x, y, titleHeight, attrHeight, width, bgColor} = targetNode
      const startX = x, startY = y + titleHeight + attrHeight * attrIndex
      canvas.eventCtx.save()
      canvas.eventCtx.fillStyle = `${bgColor}4D`
      canvas.eventCtx.moveTo(startX, startY)
      canvas.eventCtx.fillRect(startX, startY, width, attrHeight)
      canvas.eventCtx.fill()
      canvas.eventCtx.restore()
      // 绘制相关连线高亮
      for (let i = 0; i < data.relationship.length; i++) {
        const line = data.relationship[i]
        if (line.from === hoverTargetId || line.to === hoverTargetId) {
          const {
            startX,
            startY,
            endX,
            endY,
            type,
          } = lineMap.get(line.id)
          new Line(canvas.eventCtx, {
            startX,
            startY,
            endX,
            endY,
            type,
            lineWidth: 2,
          }).draw()
        }
      }
    } else if (nodeMap.get(hoverTargetId)) {
      // 绘制当前节点相关连线
      const nodeAndAttrIds = [hoverTargetId].concat(nodeMap.get(hoverTargetId).attributes.map(i => i.id))
      for (let i = 0; i < data.relationship.length; i++) {
        const line = data.relationship[i]
        if (nodeAndAttrIds.includes(line.from) || nodeAndAttrIds.includes(line.to)) {
          const {
            startX,
            startY,
            endX,
            endY,
            type,
          } = lineMap.get(line.id)
          new Line(canvas.eventCtx, {
            startX,
            startY,
            endX,
            endY,
            type,
            lineWidth: 2,
          }).draw()
        }
      }
    }
  }

  // eventCanvas常驻图形
  private renderPermanentEventCanvas() {
    const {
      activeTargets,
      canvas,
      attrMap,
      lineMap,
      data,
    } = this
    this.canvas.clearEventCanvas()
    if (!activeTargets) return

    for (let i = 0; i < activeTargets.length; i++) {
      const targetId = activeTargets[i]

      if (attrMap.get(targetId)) {
        const targetNode = attrMap.get(targetId)
        const attrIndex = targetNode.attributes.findIndex(it => it.id === targetId)
        // 绘制属性高亮
        const { x, y, titleHeight, attrHeight, width, bgColor } = targetNode
        const startX = x, startY = y + titleHeight + attrHeight * attrIndex
        canvas.eventCtx.save()
        canvas.eventCtx.fillStyle = `${bgColor}4D`
        canvas.eventCtx.moveTo(startX, startY)
        canvas.eventCtx.fillRect(startX, startY, width, attrHeight)
        canvas.eventCtx.fill()
        canvas.eventCtx.restore()
        // 绘制相关连线高亮
        for (let i = 0; i < data.relationship.length; i++) {
          const line = data.relationship[i]
          if (line.from === targetId || line.to === targetId) {
            const {
              startX,
              startY,
              endX,
              endY,
              type,
            } = lineMap.get(line.id)
            new Line(canvas.eventCtx, {
              startX,
              startY,
              endX,
              endY,
              type,
              lineWidth: 2,
            }).draw()
          }
        }
      }
    }
  }

  private findTarget (e: CanvasEvent, targetArray: Array<Table | Relationship>, trigger = false) {
    // 应该倒着循环
    const fabrics = targetArray.reverse()
    for (let i = 0; i < fabrics.length; i++) {
      const fabricsId = fabrics[i].id
      const fabric = this.nodeMap.get(fabricsId) || this.lineMap.get(fabricsId)
      const targetId = fabric?.containsPoint(e, trigger)
      
      if (targetId) return targetId
    }
  }

  private getFabricWithId (id: Id) {
    const {
      lineMap,
      attrMap,
      nodeMap,
    } = this
    return lineMap.get(id) || attrMap.get(id) || nodeMap.get(id)
  }

  private onMouseUp (e: MouseEvent, canvasEv: CanvasEvent) {
    // console.log('up', e, this)
  }

  private onMouseDown (e: MouseEvent, canvasEv: CanvasEvent) {
    const {
      data,
      lineMap,
      attrMap,
      nodeMap,
    } = this

    this.activeTargets = []

    const targetId = this.findTarget(canvasEv, [...data.table, ...data.relationship], true)
    if (targetId) {
      let eventName
      if (lineMap.get(targetId)) {
        eventName = 'line:click'
      } 
      else if (attrMap.get(targetId)) {
        eventName = 'node-attr:click'
        this.activeTargets = [targetId]
      }
      else if (nodeMap.get(targetId)) {
        eventName = 'node:click'
      }
      this.emit(eventName, targetId)
    }
    this.update()

    this.renderPermanentEventCanvas()
  }

  private onMouseMove (e: MouseEvent, canvasEv: CanvasEvent) {
    if (this.canvas.isMouseDown) {
      this.canvas.setTranslate(e)
      this.canvas.clearEventCanvas()
      const graphTranslate = this.canvas.getTranslate()
      this.emit('graph:move', graphTranslate)
      this.update()
    } else {
      const targetId = this.findTarget(canvasEv, [...this.data.table, ...this.data.relationship])
      // const hoverTarget = this.getFabricWithId(targetId)

      this.hoverTargetId = targetId
      
      this.renderTemporaryEventCanvas()
      
      if (targetId) {
        this.canvas.eventEl.style.cursor = 'pointer'
      } else {
        this.canvas.eventEl.style.cursor = 'default'
      }
    }
  }

  private onWheel (e: WheelEvent) {
    const {
      deltaY,
      deltaX,
      ctrlKey,
      shiftKey,
    } = e

    const wheelConfirm = Math.abs(deltaY) > 2 || Math.abs(deltaX) > 2

    if (!wheelConfirm) return

    // 缩放
    if (ctrlKey) {
      // const zoomRate = this.canvas.zoomRate
      // let step
      // if (zoomRate > 1) {
      //   step = deltaY > 0 ? 0.1 : -0.1
      // } else {
      //   step = deltaY > 0 ? 0.05 : -0.05
      // }
      // const calcRatio = Math.max((this.canvas.zoomRate + step) * 50, 0)
      // this.zoom(calcRatio)
    }
    // 平移
    else {
      const translate = this.canvas.getTranslate()
      this.canvas.setTranslate({
        x: translate.x - deltaX,
        y: translate.y - deltaY,
      } as MouseEvent)
      this.canvas.clearEventCanvas()
      this.update()
    }
  }

  private onResize () {
    this.update()
  }

  /**
   * 
   * @param rate 范围[1, 100]
   */
  public zoom (rate: number) {
    // 50时缩放比例为1
    const fixRate = rate / 50
    this.canvas.zoom(fixRate)
    this.update()
    this.renderPermanentEventCanvas()
  }

  public fullscreen () {
    this.canvas.fullscreen()
  }

  public destroy () {
    this.unmountPlugins()
    this.unmountCanvasEvents()
    this.canvas.destroy()
    this.freeVariable()
  }

  public viewTo (id: Id) {
    if (!id) return false

    const {
      nodeMap,
      attrMap,
      lineMap,
      canvas: {width, height},
    } = this
    const target = nodeMap.get(id) || attrMap.get(id) || lineMap.get(id)
    let x, y
    if (target instanceof Line) {
      x = -target.startX
      y = -target.startY
    } else {
      x = -target.x - target.width / 2
      y = -target.y - target.height / 2
    }
    this.canvas.viewTo(x + width / 2, y + height / 2)
    this.update()
    this.renderPermanentEventCanvas()
  }
}
