import {CanvasEvent, Id} from '../../../types'
import {DropDownIcon} from '../icon'
import {isAboveLine} from '../../utils/bezier'
import {FabricObject} from '../../shared/fabric'
import { isNumber } from '../../utils/verify'

interface LineInput {
  startX?: number
  startY?: number
  endX?: number
  endY?: number
  type?: string
  lineWidth?: number
}

export class LineBezier extends FabricObject {
  private ctx: CanvasRenderingContext2D
  public id: Id
  public from: Id
  public to: Id
  public startX = 0
  public startY = 0
  public endX = 0
  public endY = 0
  public type: string

  private cp = 60
  private cp1x = 0
  private cp1y = 0
  private cp2x = 0
  private cp2y = 0
  private path: Path2D

  private strokeStyle = '#0C64EB'
  private lineDash: [number, number]
  public lineWidth = 1

  constructor (ctx: CanvasRenderingContext2D, input: LineInput) {
    super()
    this.ctx = ctx
    this.analysisOptions(input)
    this.createBezierPath()
    this.calcBoundingBox()
  }

  private analysisOptions(input) {
    if (!input) return

    const {
      startX,
      startY,
      endX,
      endY,
      type,
      lineWidth = 1,
      from,
      to,
      id,
    } = input

    if (id) this.id = id
    if (from) this.from = from
    if (to) this.to = to
    if (isNumber(startX)) this.startX = startX
    if (isNumber(startY)) this.startY = startY
    if (isNumber(endX)) this.endX = endX
    if (isNumber(endY)) this.endY = endY
    if (type) this.type = type
    if (lineWidth) this.lineWidth = lineWidth
  }

  public updateOptions(input: LineInput) {
    this.analysisOptions(input)
    this.calcBoundingBox()
  }

  private createBezierPath () {
    const {
      cp,
      startX,
      startY,
      endX,
      endY,
    } = this
    const cp1x = this.cp1x = startX + cp
    const cp1y = this.cp1y = startY
    const cp2x = this.cp2x = endX - cp
    const cp2y = this.cp2y = endY

    const path = this.path = new Path2D()
    path.moveTo(startX, startY)
    path.bezierCurveTo(cp1x, cp1y, cp2x, cp2y, endX, endY)
  }

  public draw(input?: LineInput) {
    this.analysisOptions(input)
    this.createBezierPath()

    const {
      ctx,
      strokeStyle,
      lineDash,
      lineWidth,
      path,
    } = this

    ctx.save()
    ctx.strokeStyle = strokeStyle
    if (lineWidth) ctx.lineWidth = lineWidth
    if (lineDash) ctx.setLineDash(lineDash)
    ctx.stroke(path)
    ctx.restore()

    this.drawArrow()
  }

  public containsPoint (e: CanvasEvent, trigger: boolean) {
    const {
      x: ex, y: ey,
    } = e
    const {
      // ctx,
      // path,
      startX,
      startY,
      endX,
      endY,
      cp1x,
      cp1y,
      cp2x,
      cp2y,
      id,
    } = this
    // const isContains = ctx.isPointInPath(path, ex, ey)
    // todo：不在可视范围内的无需计算
    const isContains = isAboveLine(ex, ey, {
      startX,
      startY,
      endX,
      endY,
      cp1x,
      cp1y,
      cp2x,
      cp2y,
    })
    
    return isContains ? id : false
  }

  private drawArrow () {
    const {
      ctx,
      endX,
      endY,
      strokeStyle,
      lineWidth,
    } = this
    const width = 10
    const height = 6
    const arrow = new DropDownIcon(ctx, {
      x: endX - height / 2,
      y: endY,
      width,
      height,
      rotate: 270,
      color: strokeStyle,
    })
    arrow.draw()
  }

  private calcBoundingBox () {
    const {
      startX,
      startY,
      endX,
      endY,
    } = this
    this.boundingBox = [
      [startX, startY],
      [endX, endY],
    ]
  }
}