const nodeData = originNodeData.map((it, idx) => ({
  id: it.ent_id,
  enName: it.ent_name,
  type: it.ent_type,
  attributes: [],
  borderColor: idx === 2 ? '#24A148' : '',
  textColor: idx === 2 ? '#24A148' : '',
  bgColor: idx === 2 ? '#DEFBE6' : '',
}))

originAttrData.forEach(attr => {
  const targetNode = nodeData.find(node => node.id === attr.ent_id)
  targetNode?.attributes.push({
    id: attr.attr_id,
    enName: attr.attr_name,
    // enName: attr.attr_id,
    // cnName: '1-1',
    type: attr.datatype?.replace(/\(.*\)/, '').toLocaleUpperCase(),
  })
})

const lineData = originLineData.map((line, index) => ({
  from: line.rela_source,
  to: line.rela_target,
  id: index,
  type: line.rela_type,
}))

